body {
  background: #fff;
  font-family: "Roboto", sans-serif, "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #201D1D;
  font-size: 14px;
  line-height: 143%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: "Roboto Condensed", sans-serif;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.container {
  width: 100%;
  padding: 0 16px;
  margin: 0 auto;
}

.wrapper {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 1920px;
    padding: 0 24px;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    padding: 0 32px;
  }
}
.btn {
  min-height: 44px;
  padding: 12px 24px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: #FF3214;
  color: #fff;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 11px;
  line-height: 140%;
}

.main-title {
  font-weight: bold;
  font-size: 32px;
  line-height: 112%;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #fff;
  font-family: "Roboto Condensed", sans-serif;
}

.title {
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  text-transform: uppercase;
  color: #201D1D;
  font-family: "Roboto Condensed", sans-serif;
}

.sub-title {
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  font-family: "Roboto Condensed", sans-serif;
}

.sec {
  padding-top: 44px;
  padding-bottom: 44px;
}

.swiper-btns {
  width: 72px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.swiper-button-prev, .swiper-button-next {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #FF3214;
  position: static;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  border: 1px solid transparent;
}
.swiper-button-prev::after, .swiper-button-next::after {
  display: none;
}
.swiper-button-prev svg, .swiper-button-next svg {
  width: 20px;
  height: 20px;
  fill: white;
}
.swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {
  pointer-events: auto;
  background: transparent;
  border: 1px solid #969595;
}
.swiper-button-prev.swiper-button-disabled svg, .swiper-button-next.swiper-button-disabled svg {
  fill: #969595;
}
.swiper-pagination {
  bottom: 0;
}
.swiper-pagination.swiper-pagination-bullets .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background: #ccc;
  opacity: 1;
}
.swiper-pagination.swiper-pagination-bullets .swiper-pagination-bullet-active {
  background: #FF3214;
}

@media screen and (min-width: 768px) {
  .main-title {
    font-size: 54px;
  }

  .btn {
    min-height: 52px;
    font-size: 13px;
    line-height: 154%;
    letter-spacing: 0.3px;
  }

  .title {
    font-size: 30px;
    line-height: 133%;
  }

  .sub-title {
    font-size: 20px;
    line-height: 160%;
  }

  .sec {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .swiper-btns {
    width: 88px;
  }
  .swiper-button-prev, .swiper-button-next {
    width: 40px;
    height: 40px;
  }
  .swiper-button-prev svg, .swiper-button-next svg {
    width: 22px;
    height: 22px;
  }
}
@media screen and (min-width: 1280px) {
  .main-title {
    font-size: 64px;
  }

  .title {
    font-size: 32px;
  }
}
@media screen and (min-width: 1440px) {
  .sec {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .main-title {
    font-size: 74px;
    line-height: 108%;
  }

  .title {
    font-size: 36px;
    line-height: 122%;
  }
}
.breadcrumbs {
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 12px;
  line-height: 133%;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.breadcrumbs__list {
  display: flex;
  align-items: center;
}
.breadcrumbs__item:not(:last-child) {
  padding-right: 12px;
  position: relative;
}
.breadcrumbs__item:not(:last-child)::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  background: url("../images/icons/arrow-right.svg") no-repeat center/contain;
}
.breadcrumbs__item:last-child {
  color: rgba(255, 255, 255, 0.6);
}

@media screen and (min-width: 768px) {
  .breadcrumbs {
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 14px;
    line-height: 143%;
    max-width: 100%;
  }
  .breadcrumbs__list {
    width: 100%;
    max-width: 1856px;
    margin: 0 auto;
  }
  .breadcrumbs__item {
    margin-right: 4px;
  }
  .breadcrumbs__item:not(:last-child) {
    padding-right: 20px;
  }
  .breadcrumbs__item:not(:last-child)::after {
    width: 16px;
    height: 16px;
    right: 0;
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: #fff;
  border-bottom: 1px solid #f3f3f3;
}
.header__inner {
  min-height: 40px;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
  align-items: center;
}
.header__burger {
  margin-right: 12px;
}
.header__logo {
  margin-right: auto;
}
.header__logo img {
  display: block;
  width: 100%;
}
.header__form {
  margin-right: 12px;
}
.header__form-field {
  width: 100%;
  padding: 10px 12px;
  display: none;
  background: #F3F3F3;
  border: none;
  font-size: 14px;
  line-height: 143%;
  color: #201D1D;
}
.header__form-btn svg {
  fill: #201D1D;
}
.header__btn {
  width: 20px;
  min-width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__btn img {
  width: 100%;
}
.header__apps {
  margin-right: 12px;
}
.header__user {
  padding-top: 3px;
}
.header__user-item {
  font-size: 11px;
  line-height: 182%;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: bold;
}
.header__user-item + .header__user-item {
  margin-left: 12px;
}
.header__nav {
  display: none;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: bold;
  font-size: 13px;
  line-height: 154%;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
.header__nav a:hover, .header__nav a:focus {
  text-decoration: underline;
}

@media screen and (min-width: 768px) {
  .header__inner {
    min-height: 80px;
  }
  .header__btn {
    width: 24px;
    height: 24px;
  }
  .header__burger {
    margin-right: 24px;
  }
  .header__logo {
    width: 168px;
  }
  .header__form {
    margin-right: 24px;
  }
  .header__apps {
    margin-right: 24px;
  }
  .header__user-item {
    font-size: 13px;
  }
}
@media screen and (min-width: 1280px) {
  .header__burger {
    display: none;
  }
  .header__nav {
    margin-right: 40px;
    display: block;
  }
  .header__nav-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .header__nav-list li {
    margin-right: 36px;
  }
  .header__nav-list li:last-child {
    margin-right: 0;
  }
  .header__form {
    flex-grow: 1;
    max-width: 453px;
    margin-left: 40px;
    margin-right: 40px;
    position: relative;
  }
  .header__form-btn {
    position: absolute;
    right: 18px;
    top: 50%;
    transform: translateY(-50%);
  }
  .header__form-btn svg {
    fill: #969595;
  }
  .header__form-field {
    display: block;
    padding: 12px 20px;
    padding-right: 50px;
  }
}
@media screen and (min-width: 1440px) {
  .header__apps {
    margin-right: 60px;
  }
  .header__nav {
    margin-right: 60px;
  }
  .header__form {
    margin-left: 60px;
    margin-right: 60px;
  }
}
@media screen and (min-width: 1600px) {
  .header__form {
    max-width: 720px;
    margin-right: 156px;
  }
