.problem__item {
  padding-left: 32px;
  padding-right: 1px;
  position: relative;
}
.problem__item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 24px;
  height: 24px;
  background: url("../images/icons/problem.svg") no-repeat center/contain;
}

.decision__item {
  padding-left: 30px;
  padding-top: 1px;
  position: relative;
}
.decision__item::before {
  content: "";
  position: absolute;
