  .footer__address {
    padding-top: 15px;
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 4;
    grid-column-end: 4;
    font-size: 14px;
    line-height: 143%;
  }
  .footer__address::before {
    display: none;
  }
  .footer__contacts {
    font-size: 16px;
    line-height: 150%;
  }
  .footer__user {
    font-size: 14px;
  }
  .footer__user br {
    display: none;
  }
  .footer__bottom {
    padding-top: 20px;
    font-size: 14px;
    position: relative;
  }
  .footer__bottom::before {
    content: "";
    position: absolute;
    top: 0;
    left: -24px;
    right: -24px;
    height: 1px;
    background: #F3F3F3;
  }
  .footer__docs {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
  }
}
